@charset "utf-8";
/*通用*/
html,
body {
  width: 100%;
  height: 100%;
  font-size: 24px !important;
}
body {
  font-family: "microsoft yahei", Arial, Helvetica, sans-serif;
  color: #333;
  margin: 0;
  padding: 0;
  border: 0;
  overflow: auto !important;
  background-color: #f5f5f5;
  -webkit-overflow-scrolling: touch;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
ul,
li,
p,
h1,
h2,
h3,
h4,
h5,
h6,
dl,
dt,
dd {
  margin: 0;
  padding: 0;
  border: none;
  list-style: none;
}
p {
  font-size: 24px;
  color: #333;
}
input {
  border: none;
}
i {
  font-style: normal;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
a {
  text-decoration: none;
  color: #fff;
}
.flex {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.fl {
  float: left;
}
.fr {
  float: right;
}
.clear::after {
  /*清浮动*/
  content: '';
  display: block;
  clear: both;
}
.big_num {
  font-size: 30px;
}
.text_r {
  text-align: right;
}
.text_l {
  text-align: left;
}
